import {Component, Renderer2} from '@angular/core';
import {IonicPage, NavController, NavParams, ViewController} from 'ionic-angular';

/**
 * Generated class for the StartFilterPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-start-filter',
  templateUrl: 'start-filter.html',
})
export class StartFilterPage {

  drvSelected: boolean = true;

  constructor(public navCtrl: NavController, public navParams: NavParams, public viewCtrl: ViewController,private renderer: Renderer2) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad StartFilterPage');
  }
  ngAfterViewInit() {
    const modalWrapper = document.querySelector('.modal-wrapper');
    if (modalWrapper) {
      this.renderer.setStyle(modalWrapper, 'height', '333px');
    }
  }
  handleDRVClick(){
    this.viewCtrl.dismiss(this.drvSelected)
    const modalWrapper = document.querySelector('.modal-wrapper');
    if (modalWrapper) {
      this.renderer.setStyle(modalWrapper, 'height', '100%');
    }
  }

}



